/*文字居中*/
.t-center {
    text-align: center;
}
/*文字居中*/
.t-right {
    text-align: right;
}
.t-ellipsis {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

